import "./App.css";
import Home from "./Home";
import Thanks from "./Thanks";
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";

function App() {
  return (
    <>
      <section class="main">
        <div class="header-icon">
          <img
            src="https://raw.githubusercontent.com/RajSolai/Response/master/data/icons/128/com.github.rajsolai.response.svg"
            alt="response-icon"
          />
        </div>
      </section>

      <Router>
        <Switch>
          <Route path="/" exact component={Home}></Route>
          <Route path="/thanks" exact component={Thanks}></Route>
        </Switch>
      </Router>
    </>
  );
}

export default App;
